import { getHideBaseStyleMixin } from '../mixins/act/hide-base-style';
import { getActClass } from './utils';


export default {
  name: 'PressActModifyPhoneDialog',
  options: {
    styleIsolation: 'shared',
  },
  mixins: [getHideBaseStyleMixin()],
  props: {
    show: {
      type: Boolean,
      default: false,
      desc: '是否显示弹窗',
      required: false,
    },
    phone: {
      type: String,
      default: '',
      desc: '电话号码',
      required: false,
    },
    code: {
      type: String,
      default: '',
      desc: '验证码	',
      required: false,
    },
    title: {
      type: String,
      desc: '弹窗标题',
      default: '修改手机号',
    },
    isDisableGetCode: {
      type: Boolean,
      default: false,
      desc: '是否获取验证码',
      required: false,
    },
    getCodeText: {
      type: String,
      desc: '获取验证码文案描述',
      default: '获取验证码',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    closeDialog() {
      this.$emit('clickCloseButton');
      this.$emit('update:show', false);
    },
    onClickButton() {
      this.$emit('clickButton');
    },
    changePhone(e) {
      this.$emit('update:phone', e.target.value);
    },
    changeCode(e) {
      this.$emit('update:code', e.target.value);
    },
    onClickGetCode() {
      this.$emit('clickGetCodeButton');
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
